
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
}
.learning-lession{
  position: absolute;
  left: 0; 
  top: 40%;
    background: aliceblue;
        padding: 10px 30px;
         width: 200px;
}
.learning-lession li{
  text-align: left;
}
@media screen and (max-width:768px){
.learning-lession{
      position: relative;
      margin: 0 auto;
}
.learning-lession li{
  text-align: center;
}
}

.main-nav ul li{
  list-style-type: none;
  display: inline-block;
  padding: .5rem;
  margin: .5rem;
}
a{
  color: #fff;
}
a.router-link-exact-active{
  color: #41b883;
}
.userp a{
